import React from "react";
import { useLocation } from 'react-router-dom';
import { MDBContainer, MDBRow, MDBCol, MDBCardImage, MDBIcon, MDBBtn,
    MDBCard, MDBCardBody, MDBCardText } from 'mdb-react-ui-kit';
import { useNavigate } from 'react-router-dom';
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";


function MentorDetails() {
    const location = useLocation();
    const navigate = useNavigate();
    const toAvailability = (props) => {
        navigate('/mentorAvailability', { state: props });
    }


    return (
        <>

            
            <MDBContainer className="vh-100" style={{marginTop:25}}>
        <AppBar position="static" style={{ background: '#2E3B55' }}>
        <Toolbar>
          <Typography
            textAlign="center"
            variant="h6"
            component="div"
            sx={{ flexGrow: 1 }}
          >
            Mentor Details
          </Typography>
        </Toolbar>
      </AppBar>
        
        <MDBRow style={{marginTop:25}}>
          <MDBCol lg="4">
            <MDBCard className="mb-8">
              <MDBCardBody className="text-center">
                <MDBCardImage
                  src= {require('./../static/images/' + location.state.image)}
                  alt="avatar"
                  className="rounded-circle"
                  style={{ width: '200px' }}
                  fluid />
                  <h3>{location.state.name}</h3>
                <h4>{[...Array(location.state.rating)].map((e, i) => <MDBIcon fas icon="star" />)}</h4>  
                <div className="d-flex justify-content-center mb-2">
                  <MDBBtn onClick={() => { toAvailability() }} className='mx-2'>View Availablity</MDBBtn>
                </div>
              </MDBCardBody>
            </MDBCard>

          </MDBCol>
          <MDBCol lg="8">
            <MDBCard className="mb-4">
              <MDBCardBody>
                <MDBRow>
                  <MDBCol sm="3">
                    <MDBCardText>Full Name</MDBCardText>
                  </MDBCol>
                  <MDBCol sm="9">
                    <MDBCardText className="text-muted"><h3>{location.state.name}</h3></MDBCardText>
                  </MDBCol>
                </MDBRow>
                <hr />
                <MDBRow>
                  <MDBCol sm="3">
                    <MDBCardText>Email</MDBCardText>
                  </MDBCol>
                  <MDBCol sm="9">
                    <MDBCardText className="text-muted">{location.state.email}</MDBCardText>
                  </MDBCol>
                </MDBRow>
                <hr />
                <MDBRow>
                  <MDBCol sm="3">
                    <MDBCardText>Location</MDBCardText>
                  </MDBCol>
                  <MDBCol sm="9">
                    <MDBCardText className="text-muted">{location.state.location}</MDBCardText>
                  </MDBCol>
                </MDBRow>
                <hr />
                <MDBRow>
                  <MDBCol sm="3">
                    <MDBCardText>Company</MDBCardText>
                  </MDBCol>
                  <MDBCol sm="9">
                    <MDBCardText className="text-muted">{location.state.company}</MDBCardText>
                  </MDBCol>
                </MDBRow>
                <hr />
                <MDBRow>
                  <MDBCol sm="3">
                    <MDBCardText>Number of People Mentored</MDBCardText>
                  </MDBCol>
                  <MDBCol sm="9">
                    <MDBCardText className="text-muted">{location.state.mentored}</MDBCardText>
                  </MDBCol>
                </MDBRow>
                <hr />
                <MDBRow>
                  <MDBCol sm="3">
                    <MDBCardText>Bio</MDBCardText>
                  </MDBCol>
                  <MDBCol sm="9">
                    <MDBCardText className="text-muted">{location.state.bio}</MDBCardText>
                  </MDBCol>
                </MDBRow>  
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>
        <MDBRow>
            <MDBCol></MDBCol>
            <MDBCol><MDBBtn onClick={() => navigate(-1)}>back</MDBBtn></MDBCol>
            <MDBCol></MDBCol>
            
        </MDBRow>
      </MDBContainer>
            {/* <MDBContainer className="mh-100">
                <MDBRow>
                    <MDBCol size='md-2'>
                        <MDBCardImage src={require('./../static/images/' + location.state.image)} fluid alt='...' />
                    </MDBCol>
                    <MDBCol className="text-start" size='md-10'>
                        <h1>Name: {location.state.name} </h1>
                        <h2>Rating: {[...Array(location.state.rating)].map((e, i) => <MDBIcon fas icon="star" />)}</h2>
                        <h2>Email: {location.state.email}</h2>
                        <h2>Location: {location.state.location}</h2>
                        <h2>Company: {location.state.company}</h2>
                        <h2>Number of people mentored: {location.state.mentored}</h2>
                        <h2>Bio: {location.state.bio}</h2>

                    </MDBCol>
                </MDBRow>
                <MDBRow>
                    <MDBCol size='md-3'>
                        <MDBBtn onClick={() => navigate(-1)}  color='info'>
                            back
                        </MDBBtn>
                    </MDBCol>
                    <MDBCol size='md-9'>
                        <MDBBtn onClick={() => { toAvailability() }} className='mx-2' color='info'>
                            View Availablity
                        </MDBBtn>
                    </MDBCol>
                </MDBRow>
            </MDBContainer> */}
            </>
    )
}
export default MentorDetails;