import React, { useState } from "react";
import {
    MDBContainer,
    MDBNavbar,
    MDBNavbarBrand,
    MDBNavbarToggler,
    MDBIcon,
    MDBNavbarNav,
    MDBNavbarItem,
    MDBNavbarLink,
    MDBCollapse,
    MDBDropdown,
    MDBDropdownItem,
    MDBDropdownMenu,
    MDBDropdownToggle
} from 'mdb-react-ui-kit';

function MentHeader() {
    const [showBasic, setShowBasic] = useState(false);
    const [email, setEmail] = useState(window.sessionStorage.getItem("userEmail"));
    const [userCred, setUserCred] = useState(window.sessionStorage.getItem("userCred"));

    function signOut() {
        console.log("signsout");
        window.sessionStorage.removeItem("userEmail");
        window.sessionStorage.removeItem("userCred");
        setEmail(null);
        setUserCred(null);
        window.location = '/'
    }
    return (
        <MDBNavbar expand='lg' light bgColor='light'>
            <MDBContainer fluid>
                <MDBNavbarBrand href='/'>MENT</MDBNavbarBrand>
                <MDBNavbarToggler
                    aria-controls='navbarSupportedContent'
                    aria-expanded='false'
                    aria-label='Toggle navigation'
                    onClick={() => setShowBasic(!showBasic)}
                >
                    <MDBIcon icon='bars' fas />
                </MDBNavbarToggler>

                <MDBCollapse navbar show={showBasic}>
                    <MDBNavbarNav className='mr-auto mb-2 mb-lg-0'>
                        <MDBNavbarItem>
                            <MDBNavbarLink href='/'>
                                Home
                            </MDBNavbarLink>
                        </MDBNavbarItem>
                        <MDBNavbarItem>
                            <MDBNavbarLink href={"/about"}>
                                About Us
                            </MDBNavbarLink>
                        </MDBNavbarItem>
                        <MDBNavbarItem>
                            <MDBNavbarLink href='/contact'>
                                Contact
                            </MDBNavbarLink>
                        </MDBNavbarItem>
                        <MDBNavbarItem>
                            <MDBNavbarLink href='/mentors'>
                                Mentors
                            </MDBNavbarLink>
                        </MDBNavbarItem>

                    </MDBNavbarNav>

                </MDBCollapse>
                <MDBCollapse navbar >
                    <MDBNavbarNav right fullWidth={false} className='mb-2 mb-lg-0'>

                        {email == null ?
                            <MDBNavbarLink href='/signin'>
                                Sign in
                            </MDBNavbarLink> :
                            <MDBDropdown>
                                <MDBDropdownToggle tag='a' >
                                    <MDBNavbarLink href='#'>
                                        {JSON.parse(userCred)[email].name}
                                    </MDBNavbarLink>
                                </MDBDropdownToggle>
                                <MDBDropdownMenu>
                                    <MDBDropdownItem link onClick={()=>window.location="/myMeetings"}>My Meetings</MDBDropdownItem>
                                    <MDBDropdownItem link onClick={()=>window.location="/profile"}>Profile</MDBDropdownItem>
                                    <MDBDropdownItem link onClick={() => signOut()}>Sign Out</MDBDropdownItem>
                                </MDBDropdownMenu>
                            </MDBDropdown>}
                        {/* <MDBNavbarItem>
                            {email==null?
                            <MDBNavbarLink href='/signin'>
                                Sign in
                            </MDBNavbarLink> : 
                            <MDBNavbarLink href='#'>
                            {JSON.parse(userCred)[email].name}
                        </MDBNavbarLink>} 
                        </MDBNavbarItem>*/}
                    </MDBNavbarNav>
                </MDBCollapse>
            </MDBContainer>
        </MDBNavbar>
    )
}

export default MentHeader;