import React, { useEffect, useState } from "react";
import { useNavigate } from 'react-router-dom';
import {
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
  MDBCardImage,
  MDBBtn,
  MDBRipple,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBDropdown,
  MDBDropdownMenu,
  MDBDropdownToggle,
  MDBDropdownItem,
  MDBContainer
} from 'mdb-react-ui-kit';
import Select from 'react-select';
import makeAnimated from 'react-select/animated';
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
function Mentors() {
  const navigate = useNavigate();
  const [data, setData] = useState('');
  const [item, setItem] = useState([]);
  const [showModal, setShowModal] = useState(false);
  let email = window.sessionStorage.getItem("userEmail");
  const animatedComponents = makeAnimated();
  const toDetails = (props) => {
    navigate('/mentorDetails', { state: props });
  }
  useEffect(() => {
    setData(require('./../static/data/mentor.json'));
  }, [])

  useEffect(() => {
    var newItem = Object.keys(data).map((k, i) => {
      return [k, data[k], data[k].rating];
    });
    setItem(newItem);
  }, [data]);
  const toggleShow = () => setShowModal(!showModal);

  const toSignIn = () => {
    window.location = '/signin'
  }

  const filterData = (param) =>{
    if(email===null){
      toggleShow()
      return
    }
    let values = []
    param.map((k)=>{
      values.push(k.value);
    });
    var newItem = Object.keys(data).map((k, i) => {
      return [k, data[k], data[k].rating];
    });

    var filteredItem = newItem.filter((element)=>{
      console.log(values);
      let result = false
      values.forEach((e)=>{
        result =  result || (e ==  element[2] || e == element[1].industry);
      });
      return result
    });

    filteredItem.length>0?setItem(filteredItem):setItem(newItem)
  }

  const sortData = (param) => {
    if(email===null){
      toggleShow()
      return
    }
    var newItem = Object.keys(data).map((k, i) => {
      return [k, data[k], data[k].rating];
    });

    newItem.sort((a, b) => {
      if (param.value === "asc")
        return a[2] - b[2];
      else
        return -(a[2] - b[2]);
    });

    setItem(newItem);
  }

  const sortOptions = [
    {
      label: 'ascending',
      value: 'asc'
    },
    {
      label: 'descending',
      value: 'des'
    }
  ]
  const options = [
    {
      label: 'INDUSTRY',
      options: [
        {
          value: 'software',
          label: 'software'
        },
        {
          value: 'hardware',
          label: 'hardware'
        }
      ]
    },
    {
      label: 'RATING',
      options: [
        {
          label: 'one',
          value: '1'
        },
        {
          label: 'two',
          value: '2'
        }, {
          label: 'three',
          value: '3'
        }, {
          label: 'four',
          value: '4'
        }, {
          label: 'five',
          value: '5'
        },
      ]
    }
  ]
  return (<>
    <MDBContainer className="mh-100" style={{marginTop:25}}>
    <AppBar position="static" style={{ background: '#2E3B55' }}>
        <Toolbar>
          <Typography
            textAlign="center"
            variant="h6"
            component="div"
            sx={{ flexGrow: 1 }}
          >
            List of Mentors
          </Typography>
        </Toolbar>
      </AppBar>
    <MDBRow style={{marginTop:20}}>
      <MDBCol md='4'>
      </MDBCol>
      <MDBCol md='6'>
      <MDBRow>
      <MDBCol md='4'>
      <Select
          closeMenuOnSelect={false}
          components={animatedComponents}
          options={sortOptions}
          label="sort by rating"
          onChange = {(e)=>sortData(e)}
          placeholder={"Sort by rating"}
        />
      </MDBCol>
      <MDBCol md='6'>
      <Select
          closeMenuOnSelect={false}
          components={animatedComponents}
          isMulti
          options={options}
          onChange = {(e)=>filterData(e)}
          label="Filter"
          placeholder={"Filter"}
        />
      </MDBCol>
      </MDBRow>
      </MDBCol>

    </MDBRow>
    <MDBRow></MDBRow>  
    <MDBRow className='row-cols-1 row-cols-md-3 g-4' style={{marginTop:5}}>
      {
        item.map((k, i) => (
          <MDBCol>
            <MDBCard key={i}>
              <MDBRipple rippleColor='light' rippleTag='div' className='bg-image hover-overlay'>
                {email != null ? <MDBCardImage src={require('./../static/images/' + k[1].image)} fluid alt='...' /> :
                  <MDBCardImage src={require('./../static/images/blurred_mentor.jpg')} fluid alt='...' />
                }
                <a>
                  <div className='mask' style={{ backgroundColor: 'rgba(251, 251, 251, 0.15)' }}></div>
                </a>
              </MDBRipple>
              <MDBCardBody>
                <MDBCardTitle>{email != null ? k[1].name : "**********"}</MDBCardTitle>
                <MDBCardText>
                  {k[1].bio}
                </MDBCardText>
                <MDBBtn onClick={() => { email != null ? toDetails(k[1]) : toggleShow() }} href='#'>View</MDBBtn>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        ))
      }
    </MDBRow>
    </MDBContainer>
    <MDBModal show={showModal} setShow={setShowModal} tabIndex='-1'>
      <MDBModalDialog>
        <MDBModalContent>
          <MDBModalHeader>
            <MDBModalTitle>Limited Access</MDBModalTitle>
            <MDBBtn className='btn-close' color='none' onClick={toggleShow}></MDBBtn>
          </MDBModalHeader>
          <MDBModalBody>Please Sign In to continue</MDBModalBody>

          <MDBModalFooter>
            <MDBBtn color='secondary' onClick={toggleShow}>
              Close
            </MDBBtn>
            <MDBBtn onClick={toSignIn}>Sign In</MDBBtn>
          </MDBModalFooter>
        </MDBModalContent>
      </MDBModalDialog>
    </MDBModal>
  </>
  )
}

export default Mentors;