import React from "react";
import {
  MDBBtn,
  MDBRow,
  MDBCol,
  MDBNavbarNav,
  MDBNavbarBrand,
  MDBContainer,
  MDBNavbarItem,
  MDBNavbar
} from "mdb-react-ui-kit";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";

function Home() {
  function loadMentors() {
    window.location = '/mentors';
  }
  return (
    <>
      <MDBContainer className="mh-100">
        <MDBRow style={{marginTop:15}}></MDBRow>
        <MDBRow>
        <AppBar position="static" style={{ background: '#2E3B55' }}>
        <Toolbar>
          <Typography
            textAlign="center"
            variant="h6"
            component="div"
            sx={{ flexGrow: 1 }}
          >
            Home
          </Typography>
        </Toolbar>
      </AppBar>
        </MDBRow>
        <MDBRow style={{marginTop:25}} className="mw-80">
          {/* <MDBCol size="md-1"></MDBCol>
          <MDBCol size="md-10"><img src={require('./../static/images/Home.jpg')} className='img-fluid shadow-4' alt='...' /></MDBCol>
          <MDBCol size="md-1"></MDBCol> */}
          <img src={require('./../static/images/Home.jpg')} className='img-fluid shadow-4' alt='...' />
        </MDBRow>
        <MDBRow style={{marginTop:25}}>
          <MDBCol size="md-1"></MDBCol>
          <MDBCol size="md-10"><MDBBtn onClick={() => loadMentors()}>VIEW MENTORS</MDBBtn></MDBCol>
          <MDBCol size="md-1"></MDBCol>
        </MDBRow>
      </MDBContainer>

    </>
  )
}
export default Home;