import React, { useState, useEffect } from 'react';

import {
  MDBBtn, MDBBtnGroup, MDBContainer, MDBRow,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
} from 'mdb-react-ui-kit';
import { useNavigate } from 'react-router-dom';
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";

function MentorAvailability() {

  const [data, setData] = useState('');
  const [basicModal, setBasicModal] = useState(false);
  const [meetingTime, setMeetingTime] = useState('');
  const [meetingDate, setMeetingDate] = useState('');
  const navigate = useNavigate();
  const scheduleMeeting = (prop1, prop2) => {
    setMeetingDate(prop1)
    setMeetingTime(prop2)
    toggleShow()
  }
  const toggleShow = () => {
    setBasicModal(!basicModal)
  };

  const confirmMeeting = () => {
    window.sessionStorage.setItem("meetingDate", meetingDate);
    window.sessionStorage.setItem("meetingTime", meetingTime);
    window.location = '/myMeetings'
  }

  useEffect(() => {
    setData(require('./../static/data/mentorAvailability.json'));
  }, [])

  return (
    <>
      
      {
        <>
        
      <MDBContainer className="mh-100" style={{marginTop:25}}>
      <AppBar position="static" style={{ background: '#2E3B55' }}>
        <Toolbar>
          <Typography
            textAlign="center"
            variant="h6"
            component="div"
            sx={{ flexGrow: 1 }}
          >
            Calendar
          </Typography>
        </Toolbar>
      </AppBar>
          
          {
          Object.keys(data).map((k) => (
            <>
              <MDBRow className="d-flex justify-content-start">{k}</MDBRow>
              <MDBRow>
                <MDBBtnGroup className='me-2' aria-label='First Group'>
                  {Object.keys(data[k]).map((j) => (
                    <MDBBtn outline onClick={() => scheduleMeeting(k, data[k][j])}>{data[k][j]}</MDBBtn>
                  ))}
                </MDBBtnGroup>
              </MDBRow>
              <hr />
              </>))
          }
          </MDBContainer>
          <MDBBtn onClick={() => navigate(-1)}>
            back
          </MDBBtn>
        </>
      }
      <MDBModal show={basicModal} setShow={setBasicModal} tabIndex='-1'>
        <MDBModalDialog>
          <MDBModalContent>
            <MDBModalHeader>
              <MDBModalTitle>Meeting Confirmation</MDBModalTitle>
              <MDBBtn className='btn-close' color='none' onClick={toggleShow}></MDBBtn>
            </MDBModalHeader>
            <MDBModalBody>Do you want to confirm the meeting with your mentor on {meetingDate} at {meetingTime}?</MDBModalBody>

            <MDBModalFooter>
              <MDBBtn color='secondary' onClick={toggleShow}>
                Close
              </MDBBtn>
              <MDBBtn onClick={confirmMeeting}>Confirm</MDBBtn>
            </MDBModalFooter>
          </MDBModalContent>
        </MDBModalDialog>
      </MDBModal>

    </>
  );
}

export default MentorAvailability;