import React, { useState } from "react";
import 'mdb-react-ui-kit/dist/css/mdb.min.css';
import {
  MDBRow,
  MDBCol,
  MDBInput,
  MDBTextArea,
  MDBBtn,
  MDBRadio,
  MDBTabs,
  MDBTabsItem,
  MDBTabsLink,
  MDBTabsContent,
  MDBTabsPane,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBContainer
} from 'mdb-react-ui-kit';
import TextField from "@mui/material/TextField";
import Alert from '@mui/material/Alert';

function SignIn() {
  const [loginRegisterActive, setLoginRegisterActive] = useState('login');
  const [mentorOrMentee, setMentorOrMentee] = useState('');
  const [loginEmail, setLoginEmail] = useState('');
  const [loginPwd, setLoginPwd] = useState('');
  const [name, setName] = useState(null);
  const [email, setEmail] = useState(null);
  const [pwd, setPwd] = useState(null);
  const [cpwd, setCpwd] = useState(null);
  const [bio, setBio] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [error, setError] = useState(false);
  const [invalid, setInValid] = useState(null);

  function onSubmitHandlerRegister() {
    //console.log('onsubmit')

    const menteeDetails = {
      [email]: {
        "name": name,
        "email": email,
        "password": pwd,
        "bio": bio
      }
    };
    if (pwd === cpwd) {
      let userDetails = window.localStorage.getItem("userDetails") !== null ? JSON.parse(window.localStorage.getItem("userDetails")) : {};
      userDetails[[email]] = menteeDetails[[email]]
      console.log(userDetails);
      window.localStorage.setItem("userDetails", JSON.stringify(userDetails));
      window.sessionStorage.setItem("userCred", JSON.stringify(menteeDetails));
      window.sessionStorage.setItem("userEmail", email);
      window.location = "/"
    }


  }

  const disableButton = () => {
    if (email && name && pwd && cpwd && bio && cpwd === pwd && error === false) {
      return false
    }
    return true
  }
  const validateEmail = (value) => {
    setEmail(value);
    if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value))
      setError(true);
    else {
      setError(false);
    }
  }

  const toggleShow = () => setShowModal(!showModal);

  function onSubmitHandlerLogin() {
    let userDetails = JSON.parse(window.localStorage.getItem("userDetails"));
    if (!(loginEmail in userDetails) || loginPwd !== userDetails[loginEmail]['password']) {
      console.log("invalid credentials");
      setInValid(true)
    }
    else {
      console.log(JSON.stringify(userDetails[loginEmail]))
      window.sessionStorage.setItem("userCred", JSON.stringify({ [loginEmail]: userDetails[loginEmail] }));
      window.sessionStorage.setItem("userEmail", loginEmail);
      window.location = "/"
    }
  }

  return (
    <div>
      <MDBContainer className="vh-100">
        <div className="d-flex align-items-start bg-light mb-3" >
          <MDBCol></MDBCol>
          <MDBCol>
            <MDBTabs pills justify className='mb-3'>
              <MDBTabsItem>
                <MDBTabsLink
                  onClick={() => setLoginRegisterActive('login')}
                  active={loginRegisterActive === 'login'}
                >
                  Login
                </MDBTabsLink>
              </MDBTabsItem>
              <MDBTabsItem>
                <MDBTabsLink
                  onClick={() => setLoginRegisterActive('register')}
                  active={loginRegisterActive === 'register'}
                >
                  Register
                </MDBTabsLink>
              </MDBTabsItem>
            </MDBTabs>

            <MDBTabsContent>
              <MDBTabsPane show={loginRegisterActive === 'login'}>
                <form>
                <TextField fullWidth className='mb-4' type='email'
                      id="loginEmail"
                      label="loginEmail"
                      onChange={(e) => setLoginEmail(e.target.value)}
                      helperText={loginEmail  ? "" : 'Email is required'}
                    />
                <TextField fullWidth className='mb-4'
                      type='password'
                      id="loginPwd"
                      label="loginPwd"
                      onChange={(e) => setLoginPwd(e.target.value)}
                      helperText={loginPwd  ? "" : 'Password is required'}
                    />
                  {invalid === true ? <Alert severity="error">Invalid Credentials</Alert>  : ''} 
                  <MDBRow className='mb-4 justify-content-center'>
                    <a href='#!'>Forgot password?</a>
                  </MDBRow>

                  <MDBBtn type='button' className='mb-4' block onClick={() => onSubmitHandlerLogin()}>
                    Sign in
                  </MDBBtn>
                  <div className='text-center'>
                    <p>
                      Not a member? <a onClick={() => setLoginRegisterActive('register')} className='text-primary'>Register</a>
                    </p>
                  </div>
                </form>
              </MDBTabsPane>
              <MDBTabsPane show={loginRegisterActive === 'register'}>
                <MDBRadio name='inlineRadio' onClick={() => setMentorOrMentee('mentee')} label='Mentee' inline />
                <MDBRadio name='inlineRadio' onClick={() => setMentorOrMentee('mentor')} label='Mentor' inline />

                <MDBTabsPane show={mentorOrMentee === 'mentee'}>
                  <form>
                    <TextField fullWidth className='mb-4'
                      error={name !== null && name.length === 0}
                      id="outlined-error-helper-text"
                      label="Name"
                      onChange={(e) => setName(e.target.value)}
                      helperText={name !== null && name.length === 0 ? "Name is a required field" : ''}
                    />
                    <TextField fullWidth className='mb-4'
                      error={error !== null && error === true}
                      id="outlined-error-helper-text"
                      label="Email"
                      onChange={(e) => validateEmail(e.target.value)}
                      helperText={error !== null && error === true ? "Valid email is required" : ''}
                    />
                    <TextField fullWidth className='mb-4'
                      type='password'
                      error={pwd !== null && pwd.length < 6}
                      id="outlined-error-helper-text"
                      label="Password"
                      onChange={(e) => setPwd(e.target.value)}
                      helperText={pwd !== null && pwd.length < 6 ? "Password must be a minimum of 6 characters" : ''}
                    />
                    <TextField fullWidth className='mb-4'
                      type='password'
                      error={cpwd !== null && cpwd !== pwd}
                      id="outlined-error-helper-text"
                      label="Confirm Password"
                      onChange={(e) => setCpwd(e.target.value)}
                      helperText={cpwd !== null && cpwd !== pwd ? "Confirm password does not match" : ''}
                    />
                    <TextField fullWidth className='mb-4' multiline
                      error={bio !== null && bio.length === 0}
                      id="outlined-error-helper-text"
                      label="Bio"
                      onChange={(e) => setBio(e.target.value)}
                      helperText={bio !== null && bio.length === 0 ? "Bio is a required field" : ''}
                    />
                    <MDBBtn type="button" className='mb-4' block onClick={() => onSubmitHandlerRegister()} disabled={disableButton()}>
                      Sign up
                    </MDBBtn>
                  </form>
                </MDBTabsPane>
                <MDBTabsPane show={mentorOrMentee === 'mentor'}>
                <form>
                  <Alert severity="warning">Mentor Registration is not implemented</Alert>
                </form>
                </MDBTabsPane>
              </MDBTabsPane>
            </MDBTabsContent></MDBCol>
          <MDBCol></MDBCol>

        </div>
      </MDBContainer>
    </div>
  )
}
export default SignIn;