import React, { useEffect, useState } from "react";
import {
  MDBBadge, MDBBtn, MDBTable, MDBTableHead, MDBTableBody,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter, MDBTextArea
} from 'mdb-react-ui-kit';
import Rating from '@mui/material/Rating';

function Meetings() {
  const [meetingTime, setMeetingTime] = useState('');
  const [meetingDate, setMeetingDate] = useState('');
  const [showUpcoming, setShowUpcoming] = useState('');
  const [basicModal, setBasicModal] = useState(false);
  const [rating, setRating] = useState(0);
  const toggleShow = () => {
    setBasicModal(!basicModal)
  };
  useEffect(() => {
    if (window.sessionStorage.getItem('meetingDate') != null) {
      setMeetingTime(window.sessionStorage.getItem('meetingTime'));
      setMeetingDate(window.sessionStorage.getItem('meetingDate'));
      setShowUpcoming(true);
    }
  }, [])

  function onStarClick(nextValue, prevValue, name) {
    console.log('test')
    setRating(nextValue);
  }

  return (
    <>
      <h1>My Meetings</h1>
      {
        showUpcoming ?
          <>
            <hr />
            <h3>Upcoming Meetings</h3>
            <MDBTable align='middle'>
              <MDBTableHead>
                <tr>
                  <th scope='col'><h5>Name</h5></th>
                  <th scope='col'><h5>Date</h5></th>
                  <th scope='col'><h5>Time</h5></th>
                </tr>
              </MDBTableHead>
              <MDBTableBody>
                <tr className='table-active'>
                  <td>
                    <div>
                      <img
                        src={require('./../static/images/m4.PNG')}
                        alt=''
                        style={{ width: '100px', height: '100px' }}
                        className='rounded-circle'
                      />
                      <div>
                        <h4>Firstname4 Lastname4</h4>
                        <p className='text-muted mb-0'>mentor4@gmail.com</p>
                      </div>
                    </div>
                  </td>
                  <td>
                    <h5>{meetingDate}</h5>
                  </td>
                  <td>
                    <h5>{meetingTime}</h5>
                  </td>
                </tr>
              </MDBTableBody>
            </MDBTable>
            <hr />
          </>
          : <hr />
      }
      <h3>Past Meetings</h3>
      <MDBTable align='middle'>
        <MDBTableHead>
          <tr>
            <th scope='col'><h5>Name</h5></th>
            <th scope='col'><h5>Date</h5></th>
            <th scope='col'><h5>Time</h5></th>
            <th scope='col'><h5>Feedback</h5></th>
          </tr>
        </MDBTableHead>
        <MDBTableBody>
          <tr>
            <td>
              <div>
                <img
                  src={require('./../static/images/m1.PNG')}
                  alt=''
                  style={{ width: '100px', height: '100px' }}
                  className='rounded-circle'
                />
                <div>
                  <h4>Firstname1 Lastname1</h4>
                  <p className='text-muted mb-0'>mentor1@gmail.com</p>
                </div>
              </div>
            </td>
            <td>
              <h5>Monday, 01-01-2021</h5>
            </td>
            <td>
              <h5>10:00 - 11:00</h5>
            </td>
            <td>
              <MDBBtn onClick={toggleShow} color='link' rounded size='sm'>
                Review
              </MDBBtn>
            </td>
          </tr>
          <tr>
            <td>
              <div>
                <img
                  src={require('./../static/images/m2.PNG')}
                  alt=''
                  style={{ width: '100px', height: '100px' }}
                  className='rounded-circle'
                />
                <div>
                  <h4>Firstname2 Lastname2</h4>
                  <p className='text-muted mb-0'>mentor2@gmail.com</p>
                </div>
              </div>
            </td>
            <td>
              <h5>Tuesday, 01-02-2021</h5>
            </td>
            <td>
              <h5>14:00 - 15:00</h5>
            </td>

            <td>
              <MDBBtn onClick={toggleShow} color='link' rounded size='sm'>
                Review
              </MDBBtn>
            </td>
          </tr>
          <tr>
            <td>
              <div>
                <img
                  src={require('./../static/images/m3.PNG')}
                  alt=''
                  style={{ width: '100px', height: '100px' }}
                  className='rounded-circle'
                />
                <div>
                  <h4>Firstname3 Lastname3</h4>
                  <p className='text-muted mb-0'>mentor3@gmail.com</p>
                </div>
              </div>
            </td>
            <td>
              <h5>Wednesday, 01-03-2021</h5>
            </td>
            <td>
              <h5>20:30 - 21:30</h5>
            </td>
            <td>
              <MDBBtn onClick={toggleShow} color='link' rounded size='sm'>
                Review
              </MDBBtn>
            </td>
          </tr>
        </MDBTableBody>
      </MDBTable>
      <MDBModal show={basicModal} setShow={setBasicModal} tabIndex='-1'>
        <MDBModalDialog>
          <MDBModalContent>
            <MDBModalHeader>
              <MDBModalTitle>Mentor Feedback</MDBModalTitle>
              <MDBBtn className='btn-close' color='none' onClick={toggleShow}></MDBBtn>
            </MDBModalHeader>
            <MDBModalBody>
              <h4>Feedback</h4>
              <MDBTextArea></MDBTextArea>
              <h4>Rating</h4>
              <Rating
        name="simple-controlled"
        value={rating}
        onChange={(event, newValue) => {
          setRating(newValue);
        }}
      />
            </MDBModalBody>

            <MDBModalFooter>
              <MDBBtn color='secondary' onClick={toggleShow}>
                Close
              </MDBBtn>
              <MDBBtn onClick={toggleShow} >Confirm</MDBBtn>
            </MDBModalFooter>
          </MDBModalContent>
        </MDBModalDialog>
      </MDBModal>
    </>
  )
}
export default Meetings